<template>
	<div>
		<div class="add-container">
			<div class="add-title">
				<h1>修改资料</h1>
			</div>
			<div class="add-box">
				<Form ref="formInfo" :model="formData" label-position="left" :label-width="100" :rules="ruleInline">
					<FormItem label="店铺名称" prop="shopName">
						<i-input v-model="formData.shopName" :disabled="isVerified" size="large"></i-input>
					</FormItem>
					<FormItem label="负责人" prop="ownerName">
						<i-input v-model="formData.ownerName" :disabled="isVerified" size="large"></i-input>
					</FormItem>
					<FormItem label="联系电话" prop="phone">
						<i-input v-model="formData.phone" :disabled="isVerified" size="large"></i-input>
					</FormItem>
					<FormItem label="店铺地址" prop="address">
						<i-input v-model="formData.address" :disabled="isVerified" size="large"></i-input>
					</FormItem>
					<FormItem label="身份证号" prop="idCardNo">
						<i-input v-model="formData.idCardNo" :disabled="isVerified" size="large"></i-input>
					</FormItem>
					<FormItem label="营业执照号" prop="businessNo">
						<i-input v-model="formData.businessNo" :disabled="isVerified" size="large"></i-input>
					</FormItem>
					<FormItem label="取货车牌" prop="CarNumber">
						<i-input v-model="formData.CarNumber" size="large"></i-input>
					</FormItem>
				</Form>
			</div>
			<div class="add-submit">
				<Button type="primary" @click="submitInfo">确认修改</Button>
			</div>
		</div>

		<div class="add-container">
			<div class="add-title">
				<h1>修改密码</h1>
			</div>
			<div class="add-box">
				<Form ref="formPwd" :model="formValidate" :rules="ruleValidate" label-position="left" :label-width="100">
					<FormItem label="旧密码" prop="oldPassword">
						<i-input type="password" v-model="formValidate.oldPassword" clearable size="large" placeholder="请输入旧密码"></i-input>
					</FormItem>
					<FormItem label="新密码" prop="password">
						<i-input type="password" v-model="formValidate.password" clearable size="large" placeholder="请输入新密码"></i-input>
					</FormItem>
					<FormItem label="确认新密码" prop="rePassword">
						<i-input type="password" v-model="formValidate.rePassword" clearable size="large" placeholder="请再次输入新密码"></i-input>
					</FormItem>
				</Form>
			</div>
			<div class="add-submit">
				<Button type="primary" @click="submitPwd">确认修改</Button>
			</div>
		</div>
	</div>
</template>

<script>
	import {mapState, mapMutations} from "vuex";

	export default {
		name: 'UserInfo',

		data() {
			const validatePassCheck = (rule, value, callback) => {
				if (value !== this.formValidate.password) {
					callback(new Error('两次输入的密码不一样'));
				} else {
					callback();
				}
			};

			return {
				formData: {
					isVerified: false,
					shopName: '',
					ownerName: '',
					phone: '',
					address: '',
					idCardNo: '',
					businessNo: '',
					CarNumber: '',
				},
				ruleInline: {
					shopName: [
						{required: true, message: '请输入店铺名称', trigger: 'blur'}
					],
					ownerName: [
						{required: true, message: '请输入负责人姓名', trigger: 'blur'}
					],
					address: [
						{required: true, message: '请输入店铺地址', trigger: 'blur'}
					],
					phone: [
						{required: true, message: '手机号不能为空', trigger: 'blur'},
						{type: 'string', pattern: /^1[3|4|5|7|8|9][0-9]{9}$/, message: '手机号格式出错', trigger: 'blur'}
					],
					idCardNo: [
						{required: true, message: '请输入身份证号码', trigger: 'blur'}
					],
					businessNo: [
						{required: true, message: '请输入营业执照号码', trigger: 'blur'}
					],
					CarNumber: [
						{required: false},
					],
				},
				formValidate: {
					oldPassword: '',
					password: '',
					rePassword: '',
				},
				ruleValidate: {
					oldPassword: [
						{required: true, message: '旧密码不能为空', trigger: 'blur'},
						{type: 'string', min: 6, message: '密码长度不能小于6', trigger: 'blur'}
					],
					password: [
						{required: true, message: '新密码不能为空', trigger: 'blur'},
						{type: 'string', min: 6, message: '密码长度不能小于6', trigger: 'blur'}
					],
					rePassword: [
						{required: true, message: '请再次输入新密码', trigger: 'blur'},
						{validator: validatePassCheck, trigger: 'blur'}
					]
				}
			};
		},

		computed: {
			...mapState(["userInfo"]),
		},

		mounted() {
			this.formData.shopName = this.userInfo.accattach.NickName;
			this.formData.ownerName = this.userInfo.accattach.TrueName;
			this.formData.phone = this.userInfo.accattach.Mobile;
			this.formData.address = this.userInfo.accattach.Address;
			this.formData.idCardNo = this.userInfo.accattach.IDCardNo;
			this.formData.businessNo = this.userInfo.accattach.BusinessNo;
			this.formData.CarNumber = this.userInfo.accattach.CarNumber;
			this.isVerified = this.userInfo.accattach.status === 1;
		},

		methods: {
			...mapMutations(['SET_USER_LOGIN_INFO']),

			submitInfo() {
				this.$refs['formInfo'].validate(async (valid) => {
					if (valid) {
						let userInfo = JSON.parse(localStorage.getItem('userInfo'));
						userInfo = userInfo.accattach;
						userInfo.Code = "";
						userInfo.Msg = "";
						userInfo.NickName = this.formData.shopName;
						userInfo.TrueName = this.formData.ownerName;
						userInfo.Mobile = this.formData.phone;
						userInfo.Address = this.formData.address;
						userInfo.IDCardNo = this.formData.idCardNo;
						userInfo.BusinessNo = this.formData.businessNo;
						userInfo.CarNumber = this.formData.CarNumber;
						let res = await this.$request.updateUserInfo(userInfo);
						if (res) {
							let tmp = this.userInfo;
							tmp.accattach = userInfo;
							this.SET_USER_LOGIN_INFO(tmp);
							localStorage.setItem('userInfo', JSON.stringify(tmp));
							this.$Message.success('修改成功');
						} else {
							this.$Message.error('修改失败');
						}
					} else {
						this.$Message.error('请填写正确的信息');
					}
				});
			},

			submitPwd() {
				this.$refs['formPwd'].validate(async (valid) => {
					if (valid) {
						let userInfo = JSON.parse(localStorage.getItem('userInfo'));
						userInfo = userInfo.content;
						let res = await this.$request.updatePwd(userInfo.userName, this.formValidate.oldPassword, this.formValidate.password);
						if (res) {
							this.$Message.success('修改成功');
						} else {
							this.$Message.error('修改失败');
						}
					} else {
						this.$Message.error('请填写正确的信息');
					}
				});
			},
		},
	};
</script>

<style scoped>
	.add-container {
		margin: 15px auto 120px auto;
		width: 60%;
		min-width: 600px;
	}

	.add-title {
		margin-bottom: 15px;
		text-align: center;
	}

	.add-submit {
		display: flex;
		justify-content: center;
	}
</style>



